.loader-wrap{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    background: red;
    background: #333 url("data:image/gif;base64,");
	z-index: 999;
}
.loader{
	font-size: 0;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translateX(-50%) translateY(-50%);
	width: 120px;
	height: 20px;
}
.loader:after{
	color: rgba(0,183,229,0.9);
	content: "Загрузка...";

	font-size: 20px;
	position: absolute;
	width: 120px;
	top: 40px;
	left: 5px;
	text-align: center;
}
[class*='box-']{
	display: inline-block;
	font-size: 15px;
	line-height: 20px;
	margin-right: 5px;
	height: 20px;
	width: 20px;
	border: 5px solid rgba(111, 116, 117, 0.9);
	border-right: 5px solid transparent;
	border-left: 5px solid transparent;
	box-shadow: 0 0 10px #2187e7;
	border-radius: 50%;
	transform: scale(0.7);
	animation: loader 1s infinite linear;
}
[class*='box-']:last-child{
	margin-right: 0;
}
.box-1{
	animation-delay: .4s;
}
.box-2{
	animation-delay: .3s;
}
.box-3{
	animation-delay: .2s;
}

@keyframes loader{
    0% {
        transform: scale(1.2);
        opacity: 1;
    }

    100% {
        transform: scale(0.7) rotate(10deg);
        opacity: 0.1;
    }
}